@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --shadow-size: 0px 0px 10px 1px rgba(0, 0, 0, 0.4);
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    opacity: 1;
  }
  .underline:hover::before {
    width: 47%;
  }
  .underline:hover::after {
    width: 47%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.3s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.3s ease-in-out;
  }
  .underline-w:hover {
    opacity: 1;
  }
  .underline-w:hover::before {
    width: 47%;
  }
  .underline-w:hover::after {
    width: 47%;
  }
  .underline-w::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
.services__body {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 60px;
}
@media screen and (max-width: 1440px) {
  .services__body {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
}
@media screen and (max-width: 690px) {
  .services__body {
    grid-template-columns: repeat(1, 1fr);
  }
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .services__body {
    gap: calc(20px + 40 * (100vw - 1440px) / 480);
  }
}
.services__item {
  position: relative;
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .services__item:nth-child(n+2) .card {
