    min-height: calc(205px + 60 * (100vw - 1440px) / 480);
  }
}
.services__item:nth-child(1) {
  border-radius: 30px;
  display: flex;
  align-items: center;
  padding: 90px 15px 90px 30px;
  padding: 0 15px 0 30px;
  overflow: hidden;
  background: #2b3769;
}
.services__item:nth-child(1) .services__column {
