  transform: rotate(0);
}
.services__item:nth-child(2) .card:nth-child(1):hover .card__icon:hover {
  transition: all 0.4s ease 0s;
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .services__item:nth-child(2) .card:nth-child(1) {
    min-height: calc(68px + 32 * (100vw - 1440px) / 480);
    max-height: calc(68px + 32 * (100vw - 1440px) / 480);
    padding: calc(15px + 12 * (100vw - 1440px) / 480) calc(20px + 10 * (100vw - 1440px) / 480);
  }
}
@media screen and (max-width: 1440px) {
  .services__item:nth-child(2) .card:nth-child(1) {
