}
.services__button {
  right: 0;
  bottom: 0;
  background: #ffffff;
  border: 1px solid transparent;
  position: absolute;
  transform: translateY(15%);
}
.services__button:hover {
  border: 1px solid #fff;
}
.services__button:hover .icon {
  transform: rotate(-45deg);
}
.services__button:hover span {
