.card {
  cursor: pointer;
  height: 100%;
  border-radius: 30px;
  padding: 34px 30px;
  background: url("../img/main/card-item-bg.png") center/cover no-repeat;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: all 0.5s ease-in-out;
}
.card:hover {
  box-shadow: 0px 2px 7px #929292;
}
.card:hover .card__icon {
  transform: rotate(45deg);
}
@media screen and (max-width: 1440px) {
  .card {
    border-radius: 15px;
  }
}
.card:not(:last-child) .card__header {
  margin-bottom: 0;
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .card {
    border-radius: calc(20px + 10 * (100vw - 1440px) / 480);
    padding-left: calc(20px + 10 * (100vw - 1440px) / 480);
    padding-right: calc(20px + 10 * (100vw - 1440px) / 480);
  }
}
@media screen and (max-width: 690px) {
  .card {
    border-radius: 15px;
    padding: 15px;
  }
}
@media screen and (max-width: 490px) {
